.cart-manage .cart-content {
  margin-top: 1.066667rem;
  background-color: #f2f2f2;
  margin-bottom: 2rem;
  overflow-y: auto;
}

.cart-manage .cart-content .cart-content-top {
  border-top: 0.026667rem solid #efefef;
  height: 0.8rem;
  padding: 0 0.266667rem;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-manage .cart-content .cart-content-top .cart-content-top-left {
  color: #7a7a7a;
}

.cart-manage .cart-content .cart-content-top .cart-content-top-right {
  font-weight: bold;
  color: #d87ec6;
}

.cart-manage .cart-content .cart-goods .cart-goods-item {
  margin-bottom: 0.133333rem;
  background-color: #fff;
  width: 100vw;
  height: 2.986667rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.8rem 0 0.533333rem;
}

.cart-manage .cart-content .cart-goods .cart-goods-item img {
  width: 1.76rem;
  height: 1.76rem;
}

.cart-manage .cart-content .cart-goods .cart-goods-item .cart-goods-detail {
  width: 5.866667rem;
  color: #7a7a7a;
}

.cart-manage .cart-content .cart-goods .cart-goods-item .cart-goods-detail .cart-goods-name {
  font-size: 0.373333rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cart-manage .cart-content .cart-goods .cart-goods-item .cart-goods-detail .cart-goods-type {
  margin-top: 0.266667rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cart-manage .cart-content .cart-goods .cart-goods-item .cart-goods-detail .cart-goods-bottom {
  margin-top: 0.133333rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-manage .cart-content .cart-goods .cart-goods-item .cart-goods-detail .cart-goods-bottom .cart-goods-price span:nth-of-type(1) {
  font-size: 0.373333rem;
  font-weight: bold;
  color: #ff7f55;
}

.cart-manage .cart-content .cart-goods .cart-goods-item .cart-goods-detail .cart-goods-bottom .cart-goods-price span:nth-of-type(2) {
  text-decoration: line-through;
  color: #b0b0b0;
  margin-left: 0.133333rem;
}

.cart-manage .cart-manage-footer {
  width: 100vw;
  height: 1.813333rem;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  border-top: 0.026667rem solid #ddd;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.266667rem;
}

.cart-manage .cart-manage-footer .select-all {
  display: flex;
  align-items: center;
}

.cart-manage .cart-manage-footer .select-all span {
  margin-left: 0.106667rem;
}

.cart-manage .cart-manage-footer button {
  border: none;
  outline: none;
  border-radius: 0.133333rem;
  color: #fff;
  font-weight: bold;
}

.cart-manage .cart-manage-footer .to-collect {
  width: 3.04rem;
  height: 0.88rem;
  background-color: #ff4794;
  margin-left: 2.666667rem;
}

.cart-manage .cart-manage-footer .del {
  width: 1.973333rem;
  height: 0.88rem;
  background-color: red;
}

.iconfont {
  font-size: 0.373333rem !important;
}

.icon-xuanzhongduigou {
  color: #ff4794;
}